.footer_inner_container{
    width: 100%; background-color: #111;
    color: white;
    
}

.footer_icons{ 

    width: 12%; display: flex; 
    justify-content: space-between; 
    margin-left: 10px; color:#fff; 
}

.footer_data{ 
    display: flex; 
    justify-content: space-between;
    
}

.footer_data ul{ 
    list-style: none;
    display: flex; flex-direction: column;
    justify-content: space-around;
    height: 12vh; 
    
}

.footer_data ul li{ 
    cursor: pointer;
    border-color: #fff;
}   

.Service_code{ 
    display: flex;
    align-items: center; 
    justify-content: center; 
    border-color: #fff 1px solid;
    width: 10%;
    margin: 10px 0;
    cursor: pointer; }

.Copy-write{
    font-size: 12px;
    margin-bottom: 10px;
}